body {
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  color: #fff;
  background-color: purple;
  padding: 10px;
}

.container {
  max-width: 400px;
  margin: 20px auto;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 70%;
  margin-right: 5px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: purple;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background-color: peru;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 5px 0;
  padding: 10px;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
